@import url(app.css);

.circles {
  flex-direction: row;
  margin-top: 10rem;
  justify-content: space-evenly;
  align-items: center;
}

.circle {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 0.5rem;
  background-color: #172048;
  width: 250px;
  height: 250px;
  border-radius: 50%;
  align-items: center;
  box-shadow: 0 0 10px 0 rgb(250, 248, 248);
}

.counter {
  font-size: 3rem;
  font-weight: 600;
  background: linear-gradient( to right, #50D9DB, #007299 );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.circle-name {
  font-size: 1.1rem;
  font-weight: 600;
  color: var(--white);
}

@media screen and (max-width: 1000px) {
  .circle {
    width: 200px;
    height: 200px;
  }
  .counter {
    font-size: 2.5rem;
  }
}

@media screen and (max-width: 770px) {
  .circle {
    width: 150px;
    height: 150px;
  }
  .counter {
    font-size: 2rem;
  }

  .circle-name {
    font-size: 1rem;
  }
}

@media screen and (max-width: 630px) {
  .circle {
    width: 130px;
    height: 130px;
  }
  .counter {
    font-size: 1.5rem;
  }

  .circle-name {
    font-size: 0.9rem;
  }
}

@media screen and (max-width: 550px) {
  .circle {
    width: 100px;
    height: 100px;
  }
  .counter {
    font-size: 1.4rem;
  }
}

@media screen and (max-width: 460px) {
  .circle {
    width: 80px;
    height: 80px;
  }
  .counter {
    font-size: 1.3rem;
  }
}
